@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body,
html {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  /* display: flex; */
  font-family: "Source Sans Pro", sans-serif;
  padding: 0px;
  margin: 0px;
  height: 100%;
  width: 100%;
  min-width: 300px;
}

body {
  background-color: rgba(135, 206, 250, 0.7);
}

p {
  color: rgb(255, 255, 255);
}
.MySelect {
  display: flex;
  flex-direction: row;
  justify-content: center;
  box-sizing: border-box;
  font-size: 20px;
}

.select-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 70px;
  max-width: 300px;
  width: 100%;
  padding: 0 1em;
  font-size: 1em;
}

.my-select-label {
  font-size: 1em;
  width: inherit;
  color: rgba(0, 0, 0, 0.8);
}

.my-select-element {
  border-radius: 0.2em;
  box-sizing: border-box;
  width: inherit;
  height: 40px;
  background-color: rgb(240, 248, 255);
  border: none;
  font-size: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  padding: 0 0.2em;
}

.my-select-options {
  position: absolute;
  border-radius: 0.2em;
  margin-top: 5.5em;
  max-width: 300px;
  width: 100%;
}

.my-select-option {
  cursor: pointer;
  box-sizing: border-box;
  width: inherit;
  height: 40px;
  background-color: rgb(240, 248, 255);
  border: none;
  font-size: 1em;
  cursor: pointer;
  padding: 0 0.2em;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.my-select-option:first-child {
  border-radius: 0.2em 0.2em 0 0;
}

.my-select-option:last-child {
  border-radius: 0 0 0.2em 0.2em;
}

.my-select-option:hover {
  background-color: rgb(199, 21, 133);
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.my-select-element:focus {
  outline: none;
}

button {
  width: inherit;
  height: 40px;
  border: none;
  transition: 0.5s all ease-in-out;
  margin-top: 5px;
  background-color: rgb(70, 130, 180);
  color: rgb(255, 255, 255);
  font-size: 1em;
  cursor: pointer;
}

button:focus {
  outline: none;
}

button:active {
  transform: scale(0.95);
}

@media (min-width: 901px) {
  .select-container {
    max-width: 350px;
  }
  .my-select-options {
    max-width: 350px;
    width: 100%;
  }
}

/* internet explorer specific styles */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .my-select-options {
    margin-top: 0.25em;
  }
}
.Quote {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  text-align: center;
  width: 100%;
  max-width: 700px;
  margin: 50px auto;
  color: rgba(105, 105, 105, 0.9);
  background-image: url("https://images.unsplash.com/photo-1533628635777-112b2239b1c7?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60");
  box-shadow: 0 0.1em 0.2em gray;
  z-index: 99;
}

.Quote p {
  color: rgb(105, 105, 105);
}

blockquote {
  line-height: 40px;
  padding: 1em;
}

.overlay {
  background-color: rgba(245, 245, 245, 0.5);
  border-radius: 0.5em;
}

@media (max-width: 750px) {
  .Quote {
    max-width: 450px;
  }
  blockquote {
    padding: 0.5em;
  }
}

.Spinner > div {
  width: 18px;
  height: 18px;
  background-color: rgba(199, 21, 133, 0.6);
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.Spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.Spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100vw;
  height: 100vh;
  text-align: center;
}
.App header {
  background-color: rgb(240, 248, 255);
  height: 60px;
  color: rgb(70, 130, 180);
  display: flex;
  align-items: center;
  padding: 0px 20px;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2);
}

.App header h1 {
  font-size: 28px;
  border-left: 3px solid rgb(70, 130, 180);
  padding-left: 4px;
}

.App .app-instructions {
  text-align: center;
  width: 100%;
}

.App .categories-quote small {
  color: rgb(199, 21, 133);
}

.App .app-instructions .kwote {
  color: rgb(70, 130, 180);
}

.App footer {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 90%;
  margin: 0px auto 1px auto;
}

